body {
  font-family: sans-serif;
  background-color: black;
  text-align: center;
}
.settings {
  float: right;
}

.buttons {
  margin: 0 auto;
  border-style: solid;
  border-color: grey;
  border-width: 1px;
  padding: 20px;
}
.buttons-header {
  margin-top: 0;
  color: silver;
}
.upgrades {
  margin: 0 auto;
  border-style: solid;
  border-color: gold;
  border-width: 1px;
  padding: 20px;
}
.upgrades-header {
  margin-top: 0;
  color: gold;
}
.spacer {
  padding-bottom: 25px;
}
.click-button {
  background-color: #16a31b;
  border: none;
  color: white;
  width: 300px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 2px 2px;
  cursor: pointer;
}
.multiple-click-container {
  width: 300px;
  margin: 0 auto;
}
.multiple-click-button {
  background-color: #16a31b;
  border: none;
  color: white;
  width: 23%;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 10px;
  margin: 1px 2px;
  cursor: pointer;
}

button:disabled {
  background-color: gray;
}

.gold-button {
  background-color: gold;
  border: none;
  color: black;
  width: 300px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 2px 2px;
  cursor: pointer;
  padding: 10px;
}

.diamond-button {
  background-color: aqua;
  border: none;
  color: black;
  width: 300px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 2px 2px;
  cursor: pointer;
  padding: 10px;
}

.click-button-cost {
  margin: 0;
  padding: 0;
}

.upgrade-button {
  background-color: #16a31b;
  border: none;
  color: white;
  width: 300px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 2px 2px;
  cursor: pointer;
}

.resource {
  border: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 2px 2px;
}
